body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container{
  display: grid;
  grid-template-rows: 150px auto;
  grid-row-gap: 50px;
  justify-items : center;
}
.nav {
  background : -webkit-linear-gradient(#8fd2f7, white);
  background : linear-gradient(#8fd2f7, white);
  width: 100%;
  text-align: center;
  font-size: 3em;
}

input{
  height: 45px;
  width: 200px;
  border-bottom: 2px solid #0ca0f0;
  font-size: 1em;
}

button{
  margin-left : 20px;
  height: 50px;
  width: 200px;
  font-size: 1em;
}
